import React, {Component} from 'react';
import {Form, Icon, Input, Button, Modal} from 'antd';
const FormItem = Form.Item;

function hasErrors(fieldsError) {
    return Object.keys(fieldsError).some(field => fieldsError[field]);
}

class SpiderForm extends Component {

    handleOk = (e) => {
        console.log(e);
        this.props.appState.setVisible(false);
    }
    handleCancel = (e) => {
        console.log(e);
        this.props.appState.setVisible(false);
    }

    componentDidMount() {
        this.props.form.validateFields();
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
            }
        });
    }


    renderForm = () => {
        const {getFieldDecorator, getFieldsError, getFieldError, isFieldTouched} = this.props.form;
        const nameError = isFieldTouched('userName') && getFieldError('name');
        return (
            <Form onSubmit={this.handleSubmit}>
                <FormItem
                    validateStatus={nameError ? 'error' : ''}
                    help={nameError || ''}
                >
                    {getFieldDecorator('name', {
                        rules: [{required: true, message: '!'}],
                    })(
                        <Input prefix={<Icon type="user" style={{fontSize: 13}}/>} placeholder=""/>
                    )}
                </FormItem>
                <FormItem>
                    <Button
                        type="primary"
                        htmlType="submit"
                        disabled={hasErrors(getFieldsError())}
                    >
                        
                    </Button>
                </FormItem>
            </Form>
        )
    }

    render() {
        return (
            <Modal title="" visible={this.props.appState.visible} footer={null}
                   onOk={this.handleOk} onCancel={this.handleCancel}
            >
               <this.renderForm />

            </Modal>
        );

    }
}

export default Form.create()(SpiderForm);


